:root {
  --blue-color: #00acdc;
}

ul {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  flex: 1;
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 100%;
  margin-right: 20px;
  text-decoration: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

